<template>
    <div id="app" data-app>
        <div class="container">
            <div class="calandar">
                <p>日期</p>
                <!-- 展開日曆 -->
                <v-menu
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="290px"
                >
                <!-- 展開清單的開關 -->
                    <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                        v-model="myDates"
                        readonly
                        outlined
                        dense
                        v-bind="attrs"
                        v-on="on"
                        ></v-text-field>
                    </template>
                <!-- 展開清單的內容 -->
                    <v-date-picker
                        v-model="dates"
                        color="#72916E"
                        no-title
                        range
                        scrollable
                        locale="zh-tw"
                    >
                    </v-date-picker>
                </v-menu>
                <p v-if="error_date.length" class="error" style="position: relative; top: -25px;">
                    {{error_date.join('')}}
                </p>
            </div>
            <div>
                <p>姓名</p>
                <input type="text" placeholder="請輸入姓名" v-model="name">
                <p v-if="error_name.length" class="error">
                    {{error_name.join('')}}
                </p>
            </div>
            <div>
                <p>電話</p>
                <input type="number" name="" placeholder="請輸入電話" v-model="phone">
                <p v-if="error_phone.length" class="error">
                    {{error_phone.join('')}}
                </p>
            </div>
            <div>
                <button class="confirm_button" @click="checkform">確定預約</button>
            </div>
            <div :class="already_reserve">此日期已有人預約！</div>
        </div>
    </div>
</template>

<script>
export default {
    name: "ReserveCard",
    data: () => ({
        name: "",
        phone: "",
        dates: [],
        error_date: [],
        error_name: [],
        error_phone: [],
        ReserveData: {},
        already_reserve: 'none'
    }),
    props: ["roomid","room"],
    methods: {
        checkform(e){
            this.error_date = [];
            this.error_name = [];
            this.error_phone = [];
            // console.log(this.room)
            if(!this.dates.length){
                this.error_date.push('請點選預約日期')
            }
            if(!this.name.length){
                this.error_name.push('請輸入姓名')
            }
            if(this.phone.length != 10){
                this.error_phone.push('手機號碼是否輸入錯誤')
            }
            if(!this.error_date.length && !this.error_name.length && !this.error_phone.length){
                this.$http({
                    method: "POST",
                    headers: {
                        Accept: 'application/json',
                        Authorization: 
                        'Bearer yPMvuWWSaEcR8sF9EDuiXggEm6D9hhMkmHeE9yNryZ1QbU1BWUMHPCY8xRgl',
                        "Content-Type": "application/json",
                    },
                    url: `https://challenge.thef2e.com/api/thef2e2019/stage6/room/${this.roomid}`,
                    data: JSON.stringify({
                        name: this.name,
                        tel: this.phone,
                        date: this.dates,
                    }),
                }).then((res) => {
                    console.log(res)
                    this.ReserveData = res.data.booking;
                    // this.$emit("setReserve", this.ReserveData)
                    // console.log(this.ReserveData)
                    setTimeout(()=>{
                        this.$router.push({
                            name: 'Success', 
                            params: {
                                room: this.room,
                                ReserveData: this.ReserveData,
                            }
                        })
                    },1000)
                }).catch((err) => {
                    if(err){
                        this.already_reserve = 'already';
                    }
                });
            };
            console.log(this.ReserveData)
            e.preventDefault();
        },
    },
    computed: {
        myDates() {
            //如果dates沒有值或dates長度是0就回傳空字串
            if(!this.dates || this.dates.length === 0) {
                return "";
            }
            let str;
            //取得第1個日期的年月日
            let [year1, month1, day1] = this.dates[0].split("-");
            //自訂格式"年/月/日"
            str = `${year1}/${month1}/${day1}`;
            //如果有第2個日期就做這個if
            if(this.dates.length == 2){
            //取得第2個日期的年月日
                let [year2, month2, day2] = this.dates[1].split("-");
            //自訂格式"年/月/日"
                let str2 = `${year2}/${month2}/${day2}`;
            //判斷日期1跟日期2的大小來決定字串擺放順序
                if(this.dates[0] > this.dates[1]) {
                    str = `${str2} ~ ${str}`;
                }else if(this.dates[1] > this.dates[0]) {
                    str += ` ~ ${str2}`;
                }
            }
            //回傳最後的字串
            return str;
        },
    },
    watch: {
        // dates: {
        //     handler(){
        //         if(this.dates[0] === this.dates[1]){
        //             this.dates = this.dates[0];
        //         }
        //     }
        // }
    }
}
</script>

<style scoped>
    #app{
        width: 600px;
        margin-top: 100px;
    }
    .container{
        width: 500px;
        height: 550px;
        background-color: #E3EAE2;
        padding: 20px;
    }
    .container>div{
        width: 400px;
        margin: 0 auto;
        margin-bottom: 30px;
    }
    .container .calandar{
        margin-bottom: 0;
    }
    .container>div>p{
        margin-bottom: 3px;
    }
    input{
        width: 400px;
        height: 40px;
        border-radius: 5px;
        border: 1px solid rgba(0, 0, 0, 0.42);
        padding: 10px;
    }
    .confirm_button{
        background-color: rgb(73,97,70);
        border-radius: 5px;
        color: white;
        width: 400px;
        height: 60px;
    }
    input[type=number]::-webkit-outer-spin-button,
    input[type=number]::-webkit-inner-spin-button {
        -webkit-appearance: none;
    }
    .confirm_button:hover{
        background-color: rgba(73,97,70, 0.7);
    }
    .error{
        color: red;
    }
    .already{
        text-align: center;
        color: red;
        font-size: 24px;
        font-weight: 800;
    }
    .none{
        display: none;
    }
</style>