<template>
    <div id="app" :class="[marginTop]">
        <div class="room_title">預約房間：<p>{{room.name}}</p></div>
        <div class="room_describe">{{room.description}}
        </div>
        <div class="people_number">
            <span>房間限制人數：</span>
            <span>{{room.descriptionShort.GuestMax == room.descriptionShort.GuestMin
                            ? room.descriptionShort.GuestMin
                            : `${room.descriptionShort.GuestMin}~${room.descriptionShort.GuestMax}`}}</span>
            <span>人</span>
        </div>
        <div class="room_size">
            <span>房間大小：</span>
            <span>{{room.descriptionShort.Footage}}</span>
            <span> m<sup>2</sup></span>
        </div>
        <div class="bathroom">{{bad_bathroom}}</div>
        <div class="regulation">{{roomAmenities}}</div>
        <div class="one">
            <div>
                <div class="txt">check in 時間</div>
                <div class="time">{{room.checkInAndOut.checkInEarly}}~{{room.checkInAndOut.checkInLate}}</div>
            </div>
            <div>
                <div class="txt">最晚 checkout 時間</div>
                <div class="time">{{room.checkInAndOut.checkOut}}</div>
            </div>
        </div>
        <div :class="[notice,off]">注意</div>
        <div class="two" :class="[none]">
            <div>
                <div class="txt">平日</div>
                <div class="dollar">${{room.normalDayPrice}}</div>
            </div>
            <div>
                <div class="txt">假日</div>
                <div class="dollar">${{room.holidayPrice}}</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Roominfo",
    props:["room","marginTop","none","notice","off"],
    data: () => ({
    }),
    computed: {
        roomAmenities(){
            // wifi , 早餐 , 小吧檯 , 客房服務 , 電視 , 空調 , 冰箱 , 禁止吸煙 , 適合兒童 , 可帶寵物
            let amenitie = this.room.amenities;
            let amenities = [];
            if(amenitie){
                if(amenitie["Wi-Fi"]){
                    amenities.push('wifi');
                }
                if(amenitie["Breakfast"]){
                    amenities.push(' 早餐');
                }
                if(amenitie["Mini-Bar"]){
                    amenities.push(' 小吧檯');
                }
                if(amenitie["Room-Service"]){
                    amenities.push(' 客房服務');
                }
                if(amenitie["Television"]){
                    amenities.push(' 電視');
                }
                if(amenitie["Air-Conditioner"]){
                    amenities.push(' 空調');
                }
                if(amenitie["Refrigerator"]){
                    amenities.push(' 冰箱');
                }
                if(amenitie["Smoke-Free"]){
                    amenities.push(' 禁止吸煙');
                }
                if(amenitie["Child-Friendly"]){
                    amenities.push(' 適合兒童');
                }
                if(amenitie["Pet-Friendly"]){
                    amenities.push(' 可帶寵物');
                }
                if(amenitie["Great-View"]){
                    amenities.push(' 觀景陽台');
                }
                if(amenitie["Great-View"]){
                    amenities.push(' 觀景陽台');
                }
                if(amenitie["Sofa"]){
                    amenities.push(' 沙發');
                }
            }
            return amenities.toString();
        },
        bad_bathroom(){
            let bed = {};
            this.room.descriptionShort.Bed.forEach((item) => {
                // 當陣列中的值只有一個時
                // 一開始Object.keys(bed)還沒有塞東西，所以跑indexOf(item)出來的值只會是-1
                // 當-1<0後，會印出{Double: 1}的結果
                // 當陣列中的值有兩個時，跑第一次會印出{`${item}`:1}的結果
                // 此時再判斷一次Object.keys(bed).indexOf(item)，會印出0
                // 此時0沒有小於0，判斷為else
                // 此時bed[`${item}`]一開始為1; 
                // 再加上1就會是2
                if (Object.keys(bed).indexOf(item) < 0) {
                    bed[`${item}`] = 1;
                } else {
                    bed[`${item}`] = bed[`${item}`] + 1;
                }
            });
            let str = '';
            Object.keys(bed).forEach(item => {
                str += `${bed[item]}張${item}床`
            })
            if(this.room.descriptionShort["Private-Bath"]){
                str += `, ${this.room.descriptionShort["Private-Bath"]}間獨立衛浴`;
            }
            return str
        },
    }
}
</script>

<style scoped>
    #app{
        margin-top: 100px;
        width: 600px;   
        color: #496147;
        font-family: Segoe UI Regular;
        padding: 10px 20px 0 20px;
        margin-bottom: 100px;
    }
    .successmargin{
        transform: translateY(-110px);
    }
    .room_title{
        letter-spacing: 4.16px;
        color: #707070;
        font-size: 26px;
        font-family: Adobe Hebrew Regular;
        margin-bottom: 20px;
    }
    .room_describe{
        color: #496146;
        font-size: 16px;
        font-family: Adobe Hebrew Regular;
        margin-bottom: 10px;
    }
    .people_number,.room_size,.bathroom{
        color: #496146;
        font-size: 16px;
        font-family: Microsoft JhengHei;
        margin-bottom: 10px;
    }
    .regulation{
        background: #e3eae2 0% 0% no-repeat padding-box;
        padding: 8px;
        text-align: center;
        margin-bottom: 30px;
    }
    .txt{
        color: #496146;
        font-size: 12px;
        font-family: Microsoft JhengHei;
    }
    .dollar,.time{
        color: #496146;
        font-size: 24px;
        font-family: Adobe Hebrew Regular;
        padding: 5px 0 5px 0;
    }
    .off{
        display: none;
    }
    .notice{
        display: block;
        text-align: center;
        font-size: 24px;
        color: red;
        font-weight: 1000;
        border-top: 3px solid red;
    }
    .one,.two{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        text-align: center;
    }
    .one div>div,.two div>div{
        width: 200px;
    }
    .-none{
        display: none;
    }
</style>