        </div>
    </div>
</template>

<script>
import SuccessBanner from "../components/SuccessBanner.vue";
import SuccessCard from "../components/SuccessCard.vue";
import Roominfo from "../components/Roominfo.vue";

export default {
    name: 'Success',
    components: {
        SuccessBanner,
        SuccessCard,
        Roominfo
    },
    data: () => ({
        room: {},
        marginTop: 'successmargin',
        none: '-none',
        notice: 'notice',
        ReserveData: {},
    }),
    created(){
        this.room = this.$route.params.room;
        this.ReserveData = this.$route.params.ReserveData;
    },
}
</script>

<style scoped>
    .container{
        display: flex;
        justify-content: space-around;
        width: 1200px;
        margin: 0 auto;
    }
