<template>
    <div id="app">
        <div class="successcard">
            <div class="title">預約明細</div>
            <p><span>姓名：</span><span>{{booking[0].name}}</span></p>
            <p><span>電話：</span><span>{{booking[0].tel}}</span></p>
            <p>日期</p>
            <div class="money">
                <p>平日</p>
                <div class="mid">
                    <p>${{room.normalDayPrice}}</p>
                    <p class="x">x</p>
                    <p>{{normalday}}晚</p>
                </div>
                <p>${{room.normalDayPrice * normalday}}</p>
            </div>
            <div class="money">
                <p>假日</p>
                <div class="mid">
                    <p>${{room.holidayPrice}}</p>
                    <p class="x">x</p>
                    <p>{{holiday}}晚</p>
                </div>
                <p>${{room.holidayPrice * holiday}}</p>
            </div>
            <div class="total">合計：${{room.normalDayPrice * normalday + room.holidayPrice * holiday}}</div>
        </div>
    </div>
</template>

<script>
export default {
    name: "SuccessCard",
    props: ["booking","room"],
    computed: {
        normalday(){
            return this.booking.filter(item => {
                return [1, 2, 3, 4, 5].indexOf(new Date(item.date).getDay()) > -1;
            }).length
        },
        holiday(){
            return this.booking.filter(item => {
                return [0, 6].indexOf(new Date(item.date).getDay()) > -1;
            }).length
        }
    }
}
</script>

<style scoped>
    #app{
        width: 600px;
    }
    .successcard{
        border: 1px solid #a5bb94;
        width: 400px;
        margin:0 auto;
        color: #425752;
    }
    .title{
        font-size: 36px;
        text-align: center;
        padding-bottom: 25px;
        font-weight: 800;
    }
    .successcard>p{
        padding: 0 0 25px 20px;
        font-size: 20px;
    }
    .total{
        border-top: 1px solid #a5bb94;
        width: 90%;
        margin: 0 auto;
        text-align: end;
        font-size: 24px;
        font-weight: 800;
        padding: 15px;
        letter-spacing: 5px;
    }
    .money{
        display: flex;
        padding: 0 20px 25px 20px;
        justify-content: space-between;
    }
    .mid{
        display: flex;
        align-items: center;
        line-height: 21px;
        width: 100px;
        justify-content: center;
    }
    .x{
        flex-basis: 20px;
        text-align: center;
    }
    
</style>