<template>
    <div id="app_scroll" v-if="room">
        <p class="card_title">{{room.name}}</p>
        <div class="card" @click="goto">
            <img :src="imageUrl" alt="">
            <div class="one_row">
                <div>
                    <p class="one_row_title">人數</p>
                    <p class="one_row_txt">
                        {{
                            room.descriptionShort ? room.descriptionShort.GuestMax == room.descriptionShort.GuestMin
                            ? room.descriptionShort.GuestMin
                            : `${room.descriptionShort.GuestMin}~${room.descriptionShort.GuestMax}`
                            : ""
                        }}
                    </p>
                </div>
                <div>
                    <p class="one_row_title">床</p>
                    <p class="one_row_txt">
                        {{ room.descriptionShort ? room.descriptionShort.Bed.toString() : "" }}
                    </p>
                </div>
                <div>
                    <p class="one_row_title">大小</p>
                    <p class="one_row_txt">{{room.descriptionShort.Footage}}<span>m<sup>2</sup></span>
                    </p>
                </div>
            </div>
            <p class="two_row">{{roomAmenities}}</p>
            <div class="weekday">
                <p class="day">平日</p>
                <p class="money">${{room.normalDayPrice}}</p>
            </div>
            <div class="holiday">
                <p class="day">假日</p>
                <p class="money">${{room.holidayPrice}}</p>
            </div>
        </div>
    </div>
</template>

<script>
export default{
    name: "RoomCard",
    props: ["uid","imageUrl"],
    data: () => ({
        // room: {},
        room: {descriptionShort: {Footage: "",},},
        // amenitie: [],
    }),
    computed: {
        roomAmenities(){
            // wifi , 早餐 , 小吧檯 , 客房服務 , 電視 , 空調 , 冰箱 , 禁止吸煙 , 適合兒童 , 可帶寵物
            let amenitie = this.room.amenities;
            let amenities = [];
            if(amenitie){
                if(amenitie["Wi-Fi"]){
                    amenities.push('wifi');
                }
                if(amenitie["Breakfast"]){
                    amenities.push(' 早餐');
                }
                if(amenitie["Mini-Bar"]){
                    amenities.push(' 小吧檯');
                }
                if(amenitie["Room-Service"]){
                    amenities.push(' 客房服務');
                }
                if(amenitie["Television"]){
                    amenities.push(' 電視');
                }
                if(amenitie["Air-Conditioner"]){
                    amenities.push(' 空調');
                }
                if(amenitie["Refrigerator"]){
                    amenities.push(' 冰箱');
                }
                if(amenitie["Smoke-Free"]){
                    amenities.push(' 禁止吸煙');
                }
                if(amenitie["Child-Friendly"]){
                    amenities.push(' 適合兒童');
                }
                if(amenitie["Pet-Friendly"]){
                    amenities.push(' 可帶寵物');
                }
                if(amenitie["Great-View"]){
                    amenities.push(' 觀景陽台');
                }
                if(amenitie["Great-View"]){
                    amenities.push(' 觀景陽台');
                }
                if(amenitie["Sofa"]){
                    amenities.push(' 沙發');
                }
            }
            return amenities.toString();
        }
    },
    methods: {
        goto(){
            this.$router.push({
                name: 'Reserve', params: {room: this.room}
            })
            // console.log(this.room)
        }
    },
    created(){
        this.$http({
            method: 'get',
            headers: {
                Accept: 'application/json',
                Authorization: 'Bearer yPMvuWWSaEcR8sF9EDuiXggEm6D9hhMkmHeE9yNryZ1QbU1BWUMHPCY8xRgl'
            },
            url: 'https://challenge.thef2e.com/api/thef2e2019/stage6/room/' + this.uid,
        }).then((res) => {
            this.room = res.data.room[0];
        }).catch((err) => {
            console.log(err);
        })
    },
}
</script>

<style scoped>
    #app_scroll{
        display: flex;
        justify-content: center;
        width: 340px;
        margin: 15px 10px 30px 10px;
        font-family: Adobe Hebrew Regular;
        /* overflow-y: scroll; */
    }
    .card_title{
        letter-spacing: 6px;
        color: #496146;
        font-weight: 700;
        writing-mode: vertical-rl;
        text-orientation: sideways;
        transform: rotate(-180deg);
        text-align: right;
    }
    .card{
        display: flex;
        flex-direction: column;
        border: 1px solid black;
        box-shadow: 0 5px 5px 4px lightgray;
        cursor: pointer;
    }
    .card img{
        width: 340px;
        height: 300px;
    }
    .card>p{
        margin: 0;
        text-align: center;
    }
    .one_row{
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid lightgray;
    }
    .one_row p{
        color: #496146;
        margin: 5px;
    }
    .one_row .one_row_title{
        font-size: 12px;
        text-align: center;
    }
    .one_row .one_row_txt{
        font-size: 24px;
        text-align: center;
    }
    .two_row{
        padding: 10px 10px 0 10px;
        height: 70px;
    }
    .weekday{
        display: flex;
        position: relative;
        padding: 15px;
    }
    .weekday .day{
        margin-left: 10px;
        font-weight: 900;
    }
    .weekday .money{
        position: absolute;
        left: 50%;
        transform:translateX(-50%)
    }
    .holiday{
        background-color: #e3eae2;
        display: flex;
        position: relative;
        padding: 15px;
    }
    .holiday .day{
        margin-left: 10px;
        font-weight: 900;
    }
    .holiday .money{
        position: absolute;
        left: 50%;
        transform:translateX(-50%)
    }
</style>